/* SubCategoryNavigation.css */
/* סרגל תתי-הקטגוריות */
.sub-navigation {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
  padding: 8px;
  background-color: #fafafa;
  margin-bottom: 0.5rem; /* מפריד מהרשימה */
}

.sub-nav-topic {
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  padding: 0.4rem 0.7rem;
  background-color: #ececec;
  border-radius: 5px;
  position: relative;
  white-space: nowrap;
  cursor: pointer;
  transition: background-color 1s ease;
}

.sub-nav-topic:last-child {
  margin-right: 0;
}

.active-sub-topic {
  background-color: #666;
  color: #fff;
}

.sub-nav-underline {
  position: absolute;
  bottom: 2px;
  left: 10%;
  width: 80%;
  height: 2px;
  background-color: #fff;
}
