.modal-overlay-edit-optimal-product {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(166, 219, 171, 0.7);
}

.modal-overlay-edit-optimal-product .modal-window {
  background: #fff;
  border-radius: 5px;
  max-width: 500px;
  position: relative;
  animation: slide-down 0.5s ease;
}

.modal-overlay-edit-optimal-product .close-button-container {
  position: fixed;
  top: 60px;
  left: 10%;
  transform: translateX(-50%);
  z-index: 1;
}

.modal-overlay-edit-optimal-product .close-button {
  background: rgb(234, 68, 68);
  color: white;
  border: 2px solid rgb(244, 185, 179);
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 5px #dd4141;
  cursor: pointer;
  /*Increase the wight a little*/
  width: 40px;
}

@keyframes slide-down {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

@media (max-width: 600px) {
  .modal-window {
    width: 80%;
    height: 80%;

    overflow: auto; /* Add this line */
  }
}
